.bodystatic_splitimgtxt {
    @extend %grid-6;
    row-gap: 3rem;

    &__copy {
        display:grid;
        grid-auto-flow: rows;
        row-gap: 1.5rem;
        grid-row:1;

        @include breakpoint(w-sm) {
            grid-row:1;
        }

        

    }

    &__media {
        grid-row:1;
        @include breakpoint(w-sm) {
            grid-row:2;
        }
    }

    &_preheadline {
        font-size: 1.1rem;
        text-transform: uppercase;
        background: linear-gradient(270deg, $gradient-sunshine);
        -webkit-background-clip: text !important;
        -webkit-text-fill-color: transparent;
        display: inline-block;
    }

    &--left {
        grid-column: 1 / span 3;

        @include breakpoint (w-sm) {
            grid-column: 1 / span 6;
        }

        align-self: center;
    }

    &--right {
        grid-column: 4 / span 3;
        

        @include breakpoint (w-sm) {
            grid-column: 1 / span 6;
    


        }
        align-self: center;
    }

    &_img {

        /*set up basic text image layout styles first*/
        img {
            display: block;
            max-width: 100%;
        }

        &-frame {
            position: relative;
            padding-bottom: 2rem;

            img {
                width: calc(100% - 2rem);
                height: calc(100% - 2rem);
                border-radius: var(--roundcorner);
                position: relative;
                object-fit: cover;
            }

            &:before {
                content: "";
                width: calc(100% - 2rem);
                height: calc(100% - 2rem);
                background: linear-gradient(var(--color-charcoal-1), var(--color-charcoal-1)) padding-box, linear-gradient(89.94deg, $gradient-sunshine) border-box;
                border: 6px solid transparent;
                border-radius: var(--roundcorner);
                position: absolute;
                bottom: 0;
                right: 0;
                z-index: 0;
            }
        }
    }

    &_img_quote {
        display: grid;
        grid-template-rows: 300px 1fr;

        &--fullframe {
            grid-template-rows: auto 1fr;

            .bodystatic_splitimgtxt__imgwrap > img{
                width:100%;
                max-height: 389px;
                margin:0;
                object-fit:cover;
            }
        }

        .bodystatic_splitimgtxt_quote__content {
            border-bottom-left-radius: 32px;
            border-bottom-right-radius: 32px;
            padding: 3rem 2rem;
            background-color: var(--color-charcoal-2);
            position: relative;
        }

        .bodystatic_splitimgtxt_quote_txt {
            font-size: 1.25rem;
            margin-bottom: 1rem;
        }

        &:before {
            display: none;
        }
    }

    &_txt {
        margin-bottom: 2rem;
        border-radius: var(--roundcorner);
        padding: 3rem 2rem;
        background-color: var(--color-charcoal-2);
        position: relative;

        .bodystatic_splitimgtxt_quote_txt {
            font-size: 2rem;


        }

        &:after {
            content: "";
            width: 100%;
            height: 100%;
            border-radius: var(--roundcorner);
            border: none;
            background-image: url('data:image/png;base64,');
            background-repeat: repeat;
            position: absolute;
            top: 2rem;
            right: -2rem;
            z-index: -1;
        }

        &.bodystatic_splitimgtxt-right {
            width: calc(100% - 2rem);

            @include breakpoint (w-sm) {
                width: 100%;
            }
        }

        @include breakpoint (w-sm) {
            padding: 2rem 1.5rem;

            &:after {
                right: 0;
                left: -1rem;
                top: -1rem;
            }
        }
    }

    &__imgwrap {
        border-top-left-radius: var(--roundcorner);
        border-top-right-radius: var(--roundcorner);
        background: linear-gradient(180deg, $color-charcoal-5 0.01%, $color-charcoal-5 100%);
        position: relative;
        overflow: hidden;

        &:after {
            content: "";
            display: block;
            width: 155%;
            height: 155%;
            border: 77px solid var(--color-charcoal-2);
            border-top: 0;
            border-bottom-left-radius: 115%;
            border-bottom-right-radius: 115%;
            background: transparent;
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            bottom: -78px;
            background: transparent;
        }

        img {
            display: block;
            width: auto;
            height: 100%;
            margin: 1rem auto 0;
            border-radius: 0;
        }
    }

    &__quote_icon {
        width: 68px;
        height: 68px;
        margin: 0 auto;
        border-radius: 50%;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        background-color: var(--color-charcoal-3);
        font-family: auto;
        font-size: 6rem;
        line-height: 1.16;
        text-align: center;
        position: absolute;
        top: -34px;
        left: 50%;
        transform: translateX(-50%);
    }

    &_quote_citation {
        font-size: 1.1rem;
        font-weight: 600;
        text-align: right;
    }

    &_list_item {
        display: grid;
        grid-template-columns: 28px 1fr;
        grid-gap: 1rem;

        h3 {
            margin-bottom: 0.5rem;
        }

        p {
            font-size: 1rem;
        }

        img {
            width: 100%;
        }

        &:last-of-type {
            p:last-of-type {
                margin-bottom: 0;
            }
        }
    }

    &-green-yellow {
        .bodystatic_splitimgtxt_preheadline {
            background: linear-gradient(270deg, $gradient-green-yellow);
        }

        .bodystatic_splitimgtxt_img-frame {
            &:before {
                background: linear-gradient(var(--color-charcoal-1), var(--color-charcoal-1)) padding-box, linear-gradient(89.94deg, $gradient-green-yellow) border-box;
            }
        }

        .bodystatic_splitimgtxt_txt {
            &:after {
                background-image: url("data:image/png;base64,");
            }
        }
