.post-meta {
  @extend %grid-12;
  column-gap: 2rem;

  &__header {
    border-bottom: 2px solid var(--color-charcoal-4);
    color: var(--color-charcoal-4);
    padding-bottom: .5rem;
    margin-bottom: 1rem;
  }

  &__tags {
    grid-column: span 4;

  }

  &__bio {
    grid-column: span 8;
  }

  &__related {
    grid-column: span 12;
  }

  &__related-card-grid>span {
    display:grid;
    grid-template-columns: repeat(12, 1fr);
    column-gap:2rem;

  }

  &__related-item {
    --card-bg:var(--color-charcoal-3);
    --text-color:var(--color-white);

    grid-column: span 4;
    height: 10rem;
    @include breakpoint (w-med) {
      grid-column: span 6;
      margin-bottom: 3rem;
    }

    @include breakpoint (w-sm) {
        grid-column: span 12;
    }
  }

  &__bio-inner {
    display:grid;
    grid-template-columns: 25% 1fr;
  }

}
















.blog-author-hero--small {
  padding: 0 0 2em;
  width: 100%;
}

.blog-author-hero__title--small {
  font-size: 1.33em;
  margin-bottom: 0;
  margin-top: 0;
}

.blog-author-hero__image--small {
  grid-column: 1 / 3;
}

@media (min-width: 768px) {
  .blog-author-hero--small .blog-author-hero__copy {
    grid-column: 3 / 12;
  }
}

.blog-post-topics__list .btn {
  margin-right: 0.33em;
  margin-bottom: 0.88em;
}

.blog-body {
  width: 55em;
  max-width: 100%;
  padding-top: 0;
}

.blog-body p {
  line-height: 1.88;
}

.blog-body img {
  margin-top: 1rem;
}

.blog-body blockquote {
  margin-top: 3rem;
  margin-bottom: 3rem;
}

.blog-body h6 {
  margin-top: -2em;
}

.blog-body__inner {
  grid-column: 2 / 11;
}

.single-post-meta .blog-author-hero__intro {
  margin-top: 1.66em;
  margin-bottom: 1em;
  font-size: 1.33em;
}


.social-share svg {
  transition: ease .5s;
  max-width: 1.25rem;
  max-height: 1.25rem;
}

.social-share {
  display: grid;
  justify-items: center;
  align-items: center;
  border: 2px solid;
  border-radius: 50%;
  border-color: currentColor;
  transition: ease .5s;
  width: 3rem;
  height: 3rem;
  color: var(--color-charcoal-5);
  margin-bottom: 0.66em
}

.social-share svg path,
.social-share svg rect {
  fill: currentColor;
}

.blog-body__share-buttons {
  grid-column: 1 / 12;
  position: relative;
}

.blog-share__track {
  position: sticky;
  top: 2em;
}

html,
body {
  overflow: initial !important;
}

@media (max-width: 829px) {

  .blog-share__track {
    display: flex;
    justify-content: space-between;
    max-width: 160px;
    margin: 0 auto;
  }

}

@media (min-width: 830px) {

  .blog-body__share-buttons {
    grid-column: 1;
  }

}

@media (min-width: 930px) {

  .blog-body__share-buttons {
    left: -3em;
  }

  .blog-body__inner img {
    margin: 0 -3em;
    min-width: calc(100% + 6em);
  }

}












.news-feature--link--small .news-feature__title {
  margin-bottom: 0.66em;
}



.feature-grid--item {
  margin-bottom: 2em;
}

@media (min-width: 800px) {

  .related-grid>span {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2em;
    gap: 2em;
  }

}

@media (min-width: 960px) {

  .news-feature--link--small .card-inner {
    display: flex;
  }

  .news-feature--link--small .card-image {
    min-width: 45%;
    margin: 0;
    min-height: 100%;
    height: 9em;
  }

  .news-feature--link--small {
    padding: 0;
  }

}