.icon-block {
  @extend %grid-12;
 
    &__intro {
      grid-column: 2 /span 8;
      margin-bottom: 3rem;
      
      @include breakpoint(w-sm){
        grid-column: 1 /span 12;
      }
     
    }
  
    &__subheadline {
      margin: 1rem 0;
    }
  
    &__row {
      grid-column: 2 /span 10;
      display: grid;
      grid-auto-columns: 1fr;
      grid-column-gap: 3rem;
      
      @include breakpoint(w-sm){
        grid-template-columns: 1fr auto 1fr;
        grid-column-gap: 0;
      }
  
      // @include breakpoint(w-sm) {
      //   grid-template-columns: repeat($sm-num-cols, 1fr);
      //   max-width: 320px;
  
      //   .block-body,
      //   .block-img,
      //   .block-title {
      //     grid-row: auto !important;
      //   }
  
      //   .block-body {
      //     margin-bottom: 3rem;
      //   }
  
      // }
        &--1{
          grid-row: 2;
          margin-bottom: 3rem;

          @include breakpoint (w-sm) {
            grid-auto-flow:row;
            grid-row:auto;
            margin-bottom:0rem;
          }
       
        }
        &--2{
          grid-row: 3;
          @include breakpoint (w-sm) {
            grid-auto-flow:row;
            grid-row:auto
          }
        }
      
  
      .block-img {
        overflow: hidden;
        grid-row: 1;
        justify-self: left;
        display: flex;
        margin-bottom: 1rem;
        img {
          max-width: 3rem;
          max-height: 3rem;
          width: 100%;
        }
      }
  
      .block-title {
        grid-row: 2;
        margin-bottom: 1rem;
        font-weight:700;
      }
  
      .block-body {
        grid-row: 3;
        @include breakpoint (w-sm) {
          padding-bottom: 1.5rem;
        }
      }
      @include breakpoint (w-sm){
        .block-img, .block-title, .block-body{
          grid-row:auto;
          grid-column: 2;
        }
    
      }
      
    }
  
  }